.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.todo{
  border-bottom: 1px solid grey;
}

.item{
  display: flex;
  align-items: center;
  font-size: 22px;
  background-color: rgba(223, 223, 223, 0.911);
  margin:8px;
  border-radius: 10px;
}

.item button {
  float: left;
  background-color: red;
  border: 0;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  font-size: 20px;
  color: white;
  margin-left:auto;
  margin-right: 15px;

}

.item input{
  -ms-transform: scale(2); /* IE */
  -moz-transform: scale(2); /* FF */
  -webkit-transform: scale(2); /* Safari and Chrome */
  -o-transform: scale(2); /* Opera */
  transform: scale(2);
  padding: 10px;
}

.item button:hover{
  cursor: pointer;
}

.item button:focus{
  outline: none;
}

.nav{
  background-color: #333;
  padding: 18px;
  color: white;
  text-align: center;
}

.links a{
  color:white;
  text-decoration: none;
  font-size:20px;
}

.links{
  font-size: 20px;
}
